import React from 'react';
import PopupWithForm from './PopupWithForm';

function AddPlacePopup({ onAddPlace, isOpen, onClose }) {

  const [place, setPlace] = React.useState('');

  function handleChangePlace(e) {
    setPlace(e.target.value);
  }

  const [link, setLink] = React.useState('');

  function handleChangeLink(e) {
    setLink(e.target.value);
  }

  function handleSubmit(e) {
    e.preventDefault();
    onAddPlace({
      name: place,
      link
    })
  }

  React.useEffect(() => {
    setPlace('');
    setLink('');
  }, [isOpen]);

  return (
    <PopupWithForm
      title="–ù–æ–≤–æ–µ –º–µ—Å—Ç–æ"
      name="new-place"
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
    >
      <>
        <input
          id="input-new-place"
          type="text"
          name="input-new-place"
          className="form__input form__input_new-place"
          autoComplete="off"
          placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ"
          minLength="2"
          maxLength="30"
          required
          onChange={handleChangePlace}
          value={place ? place : ''}
        />
        <span id="input-new-place-error" className="form__input-error"></span>
        <input
          id="input-image-link"
          type="url"
          name="input-image-link"
          className="form__input form__input_image-link"
          autoComplete="off"
          placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É"
          required
          onChange={handleChangeLink}
          value={link ? link : ''}
        />
        <span id="input-image-link-error" className="form__input-error"></span>
        <button type="submit" className="form__submit-button">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
      </>
    </PopupWithForm>
  )
  }

export default AddPlacePopup;
